.CustomTable{
    border: 1px solid black;
}

.table_row_marked{
    border: 1px solid red !important;
    color:  red;
}

.CustomTable{
    width: 100%
}

.CustomTablePagingButtons{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.CustomTablePagingButtons button{
    border: None;
    background-color: white;
}

td {
  max-width: 150px;            /* Максимальная ширина ячейки */
  white-space: nowrap;         /* Запрет переноса на новую строку */
  overflow: hidden;            /* Скрыть всё, что не помещается */
  text-overflow: ellipsis;     /* Добавить многоточие */
}