.Header {
    display: flex;
    flex-direction: row;
    height: 180px;
    margin-bottom: 30px;
    padding: 30px;
}

.Header img {
    width: 110px;
    height: 150px;
}

.Header .DataBlock {
    display: flex;
    flex-direction: column;
    padding-left: 30px;
}

.Header .DataBlock .UserData {
    display: flex;
    flex-direction: row;
    height: 30px;
    gap: 30px;
    margin-bottom: 15px;
}