.ExtsX509v3_constructor {
    display: flex;
    flex-direction: column;
    height: 300px;
    overflow: scroll;
}

.ExtsX509v3_ext {
    display: grid;
    padding-top: 10px;
    grid-template-columns: 5fr 10fr 20fr;
    border-bottom: 1px solid black;
}
.ExtsX509v3_ext_viewer{
    display: grid;
    padding-top: 10px;
    grid-template-columns: 10fr 20fr;
    border-bottom: 1px solid black;
}


.ExtsX509v3_array_block {
    display: flex;
    flex-direction: row;
}

.ExtsX509v3_array_block button {
    width: fit-content;
    height: fit-content;
}

.id_at_countryName_block {
    display: flex;
    flex-direction: column;
}
.ExtsX509v3_activator{
    margin-top: 5px;
    width: 10px !important;
}
.ExtsX509v3_activator>.content_line>label{
    min-width: 0px !important;
    width: 0px !important;
}